.feedback-add-container {
   padding-top: 41px;
    width: 348px;
    // height: 540px;
    background-color: #FFFFFF;
    justify-content: center;
    // flex-direction: column;
    .helpful-and-comment {
        // height: 147px;
        flex-direction: column;
        .feedback-question {
            text-align: center;
            height: 24px;
            font-weight: 600;
            font-family: t-s-bold;
            margin-block-end: 24px;
        }
        .helpful-and-nothelpful {
            // text-align: center;
            justify-content: center;
            gap: 40px;
            margin-block-end: 41px;
            // align-items:baseline;
            cursor: pointer;
            font-size: rem(14px);
            .helpful {
                align-items: center;
                justify-content: center;
                img {
                color: #525252;
                margin-inline-end: 8px;
            }
            
        }
        .nothelpful {
            align-items: center;
            justify-content: center;
            img {
                color: #525252;
                margin-inline-end: 8px;
                }

        }
        }
    }
    .comment-container{
        text-align: center;
        flex-direction: column;
        align-items: center;    
        background-color: #FFFFFF;
        width: 348PX;
        // height: 393px;
        textarea {
            font-family: t-regular;
            font-size: rem(14px);
            font-weight: 400;
            // overflow: hidden;
            white-space: nowrap;
            background-color: #F4F4F4;
            width: 184px;
            height: 174px;
            resize: none;
            border: none;
            margin-block-end: 20px;
            padding: 16px 24px 0px 20px;
        }        
        textarea:focus, input:focus{
            outline: none;
        }
    }
    .action-container {
        width: 184px;
        margin-block-end: 45px;
        align-items: center;
        justify-content: end;
        cursor: pointer;
        .skip-button {
            margin-inline-end: 12px;
        }
        .submit-button {
        cursor: pointer;
        width: 70px;
        height: 32px;
        font-size: rem(14px);
        background-color: #0069D9;
        border: none;
        color: #FFFFFF;
    }
    }
}